<template>
	<div class="mui-numbox" data-numbox-min='1' style="height: 25px;">
		<button class="mui-btn mui-btn-numbox-minus" type="button">-</button>
    <input id="test" class="mui-input-numbox" type="number" value= "1" @change = "countChanged" ref = "numbox" readonly />
		<!-- <input id="test" class="mui-input-numbox" type="number" :value="initcount" @change = "countChanged" ref = "numbox"/> -->
		<button class="mui-btn mui-btn-numbox-plus" type="button">+</button>
	</div>
</template>

<script>
import mui from '@/assets/mui/js/mui.min.js'

export default {

  name: 'goodsinfo_numbox',

  data() {
    return {
    }
  },

  mounted () {
  	mui(".mui-numbox").numbox()
  },
  methods: {
    countChanged () {
      // this.$store.commit('updateGoodsInfo', { id: this.goodsid, count: this.$refs.numbox.value })
    }
  },
  props: ['initcount', 'goodsid']
}
</script>

<style lang="css" scoped>
</style>



